import { real } from "./js/jquery.min.js"
import { jquery } from "./js/jquery.orgchart.js"

import axios from 'axios';
import { VoBasic } from 'vue-orgchart'

export default {
    name: 'zgjx',
    inject:["reload"],
    data(){
        return{
            input: '',
            currentPage1: 1,
            currentPage2: 1,
            currentPage3: 1,
            currentPage4: 1,
            currentPage5: 1,
            total:100,
            total1:100,
            total2:100,
            total3:100,
            total4:100,
            pagesize:10,
            currpage:1,
            tableData: [],
            tableData1: [],
            tableData2: [],
            tableData3: [],
            tableData4: [],
            options: [],
            value: '',
          //  options2: [],
          //  value2: [],
            options3: [],
            value3: '',
            datasource : {},
            datatree: []
        }
    },
    methods: {
        getNewArray(data){
            // console.log(data.children,'jinru l e getnewarray')
            var newArray = [];
            var name = {};
            name.name = data.name
            name.value = data.progress
            name.children = []
            newArray.push(name);
            // console.log(newArray,'newarray')
            for (let i = 0; i < data.children.length; i++) { // children
                // console.log(newArray.children, [i])
                let aaa = {}
                aaa.name = data.children[i].name;
                aaa.value = data.children[i].progress;
                if (data.children[i].children) { // children
                    aaa.children = []
                    for (let j = 0; j < data.children[i].children.length; j++) { // children
                        let bbb = {};
                        bbb.name = data.children[i].children[j].name;
                        bbb.value = data.children[i].children[j].progress;
                        if (data.children[i].children[j].children) { // children
                            bbb.children = [];
                            for (let k = 0; k < data.children[i].children[j].children.length; k++) { // children
                                let ccc = {};
                                ccc.name = data.children[i].children[j].children[k].name;
                                ccc.value = data.children[i].children[j].children[k].progress;
                                // children
                                if (data.children[i].children[j].children[k].children){ // 
                                    ccc.children = [];
                                    for (let q = 0; q < data.children[i].children[j].children[k].children.length; q++) { // 
                                        let ddd = {};
                                        ddd.name = data.children[i].children[j].children[k].children[q].name;
                                        ddd.value = data.children[i].children[j].children[k].children[q].progress;
                                        
                                        if (data.children[i].children[j].children[k].children[q].children){ // 
                                            ddd.children = [];
                                            for (let w = 0; w < data.children[i].children[j].children[k].children[q].children.length; w++) { // 
                                                let eee = {};
                                                eee.name = data.children[i].children[j].children[k].children[q].children[w].name;
                                                eee.value = data.children[i].children[j].children[k].children[q].children[w].progress;
                                                
                                                if (data.children[i].children[j].children[k].children[q].children[w].children){ // 
                                                    eee.children = [];
                                                    for (let e = 0; e < data.children[i].children[j].children[k].children[q].children[w].children.length; e++) { // 
                                                        let fff = {};
                                                        fff.name = data.children[i].children[j].children[k].children[q].children[w].children[e].name;
                                                        fff.value = data.children[i].children[j].children[k].children[q].children[w].children[e].progress;
                                                        // 

                                                        if (data.children[i].children[j].children[k].children[q].children[w].children[e].children){ // 
                                                            fff.children = [];
                                                            for (let r = 0; r < data.children[i].children[j].children[k].children[q].children[w].children[e].children.length; r++) { // 
                                                                let ggg = {};
                                                                ggg.name = data.children[i].children[j].children[k].children[q].children[w].children[e].children[r].name;
                                                                ggg.value = data.children[i].children[j].children[k].children[q].children[w].children[e].children[r].progress;
                                                                
                                                                if (data.children[i].children[j].children[k].children[q].children[w].children[e].children[r].children){ // 
                                                                    fff.children = [];
                                                                    for (let t = 0; t < data.children[i].children[j].children[k].children[q].children[w].children[e].children[r].children.length; t++) { // 
                                                                        let hhh = {};
                                                                        hhh.name = data.children[i].children[j].children[k].children[q].children[w].children[e].children[r].children[t].name;
                                                                        hhh.value = data.children[i].children[j].children[k].children[q].children[w].children[e].children[r].children[t].progress;
                                                                        
                                                                        ggg.children.push(hhh);
                                                                    }
                                                                }
                                                                fff.children.push(ggg);
                                                            }
                                                        }
                                                        eee.children.push(fff);
                                                    }
                                                }
                                                ddd.children.push(eee);
                                            }
                                        }
                                        ccc.children.push(ddd);
                                    }
                                }
                                bbb.children.push(ccc)
                            }
                        }
                        aaa.children.push(bbb)
                    }
                }
                newArray[0].children.push(aaa)
            }
            // console.log(newArray, "newarray")
            this.theTree(newArray);
        },
        theTree(data){
            // var that = this;
            var myChart = this.$echarts.init(document.getElementById('chart-container'));
            myChart.showLoading();
            this.$echarts.util.each(data.children, function (datum, index) {
                index % 2 === 0 && (datum.collapsed = true);
            });
            console.log(data, 'echartsdata');
            myChart.hideLoading();
            var option = {
                // animation : false,    // 
                tooltip: {
                    trigger: 'item',
                    triggerOn: 'mousemove'
                },
                series: [
                    {
                        type: 'tree',
                        data: [data[0]],
                        top: '1%',
                        left: '7%',
                        bottom: '1%',
                        right: '20%',
                        symbolSize: 7,
                        label: {
                            normal: {
                                position: 'left',
                                verticalAlign: 'middle',
                                align: 'right',
                                fontSize: 11
                            }
                        },
                        leaves: {
                            label: {
                                normal: {
                                    position: 'right',
                                    verticalAlign: 'middle',
                                    align: 'left'
                                }
                            }
                        },
                        expandAndCollapse: true,
                        animationDuration: 550,
                        animationDurationUpdate: 750
                    }
                ]
            }
            // myChart.clear();    // 
            myChart.setOption(option);
        },
        toMounted(){
            var that = this; 
            var dj = localStorage.getItem("userdj");
            let usedaid = localStorage.getItem("user_id");
            console.log(usedaid,dj)
            var query = this.$route.params.core
            console.log(query)
            $.ajax({
                type: "post",
                url: this.baseUrl+"/ydjxzg/mb/jxscore.action",
                data:{
                    manager_id:usedaid,
                    dj: dj
                },
                dataType: "json",
                success: function (res) {
                    console.log(res);

                    that.options = res.target
                    that.tableData = res.xsjxf;
                    that.total = res.xsjxf.length;
                    that.tableData1 = res.bmjxf;
                    that.total1 = res.bmjxf.length;
                    that.tableData2 = res.bmbyts;
                    that.total2 = res.bmbyts.length;
                    that.tableData3 = res.qxjxf;
                    that.total3 = res.qxjxf.length;
                    that.tableData4 = res.jkryxx;
                    that.total4 = res.jkryxx.length;


                    that.options2 = res.xsjxf;
                    that.options3 = res.jkmd;


                    that.datatree = res.myscore;
                    that.getNewArray(that.datatree);
                    console.log(that.datatree);

                //     if(res.myscore){
                //         res.myscore.target_name = res.myscore.manager_name
                //         if(res.myscore.weight ==undefined ){
                //             res.myscore.weight ='1'
                //         }
                //     }
                        // that.options2 = res.xsjxf;
                        // that.options3 = res.jkmd;
                //     that.datasource = res.myscore
                //     var nodeTemplate = function(data) {
                //         return `
                //             <div class="title">${data.target_name}</div>
                //             <div class="content">${data.progress} (${data.weight})</div>
                //         `;
                //     };
                //     var oc = $('#chart-container').orgchart({
                //         'data' : res.myscore,
                //         'nodeTemplate': nodeTemplate,
                //         'nodeTitle': 'title',
                //         'createNode': function($node,data){
                //             // console.log($node,data)
                //             // document.oncontextmenu = function(e){
                //             //     return false;   //
                //             // };
                //             $node.mousedown(function(e) {
                //                 if (3 == e.which) {   //3
                //                     console.log(1232)
                //                 }
                //             })
                //         }
                //     });
                }
            }); 
            $.ajax({
                type: "post",
                url: this.baseUrl+"/ydjxzg/mb/getjtjx.action",
                data:{
                    manager_id:1
                },
                dataType: "json",
                success: function (res) {
                    console.log(res)
                }
            }); 
            // var datasource = this.datasource;
        },
      deleteRow(index,tableData4) {
          console.log(index,tableData4)
          localStorage.setItem("ycid",tableData4[index].user_id)
      },
      open2() {
        
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
             var that = this;
        let id = localStorage.getItem("userId");
        let ycid = localStorage.getItem("ycid")
        console.log(id);
        var that = this;
        $.ajax({
            type: "post",
            url: this.baseUrl+"/ydjxzg/mb/getscjk.action",
            data:{
                user_id:ycid,
                mon_id:id
            },
            dataType: "json",
            success: function (res) {
                console.log(res)
                if(res.mess == true){
                    that.$message({
                        type: 'success',
                        message: '!'
                        
                    });
                }else{
                   that.$message({
                        type: 'error',
                        message: '!'
                    }); 
                }
            // that.reload();
            that.toMounted();
            }
        })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
      },
      quxiao(e){
          this.$refs.mask.style.display = "none";
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
        this.pagesize = val
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
        this.currpage = val
      },
    //   selectTrigger2(e){
    //     console.log(e)
    //     localStorage.setItem("id",e);
    //   },
      selectTrigger3(e){
        console.log(e)
        localStorage.setItem("tid",e);
      },
      get:function(){
          var that = this;
        let id = localStorage.getItem("user_id");
        let tid = localStorage.getItem("tid")
        console.log(id)
        let formData = new FormData();
        formData.append('user_id',tid);
        formData.append('mon_id',id);
        axios.post(this.baseUrl+'/ydjxzg/mb/gettjjk.action',formData)
            .then(function (res) {
                console.log(res);
                that.$message({
                    showClose: true,
                    message: '',
                    type: 'success'
                });
                // that.reload();
                that.toMounted();
            })
        // $.ajax({
        //     type: "post",
        //     url: this.baseUrl+"/ydjxzg/mb/gettjjk.action",
        //     data:{
        //         user_id:tid,
        //         mon_id:id
        //     },
        //     dataType: "json",
        //     success: function (res) {
        //         console.log(res)
        //         // that.tableData4 = res.jkryxx;
        //         // that.total4 = res.jkryxx.length
        //        // location.reload();
        //     }
        // }); 
      },
    //   sousuo:function(){
    //     var that = this;
    //     let id = localStorage.getItem("id");
    //     console.log(id)
    //     $.ajax({
    //         type: "post",
    //         url: this.baseUrl+"/ydjxzg/mb/getjkryxx.action",
    //         data:{
    //             user_id:id
    //         },
    //         dataType: "json",
    //         success: function (res) {
    //             console.log(res)
    //             that.tableData4 = res.jkryxx;
    //             that.total4 = res.jkryxx.length
    //         }
    //     }); 
    //   }
    },
    mounted(){
        this.toMounted();
    }
}
</script>

<style scoped>
@import "./css/style.css";
@import "./css/jquery.orgchart.css";

.tab{
    width: 100%;
}
.input{
    width: 230px;
}
.sousuo{
    width: 80px;
    height: 39px;
    color: #ffffff;
    background-color: #029348;
    border: none;
    border-radius: 3px;
}
.lsousuo{
    width: 300px;
    height: 45px;
    float: right;
}
.lput{
    width: 70%;
    height: 100%;
}
.sbtn{
    width: 23%;
    height: 85%;
    border-radius: 4px;
    border: none;
    background-color: #029348;
    color: #ffffff;
}
.mask{
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 3;
    display: none;
}
.yichu{
    position: absolute;
    width: 520px;
    height: 160px;
    background-color: #ffffff;
    left: 50%;
    top: 50%;
    margin-left: -260px;
    margin-top: -80px;
}
.yichu h3{
    text-align: center;
    padding: 15px;
}
.quxiao{
    width: 80px;
    height: 30px;
    margin-left: 150px;
    margin-top: 30px;
}
.qued{
    width: 80px;
    height: 30px;
    margin-left: 60px;
}
</style>



